<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <div v-for="(o) in content" :key="o">
        <div v-for="(a) in o" :key="a">
          <router-link :to="'/' + type +'?id=' + a.id + '&type=' + type">
            <div class="list-introduce">
              <dev class="title">
                <b>{{a.title}}</b>
              </dev>
              <br/>
              <span class="img">
                <el-image :key="a.img" :src="a.img" lazy></el-image>
              </span>
              <p class="remark">
                {{a.remark.slice(0, textSize) + '...'}}
              </p>
              <span class="remark">
                <b>作者：</b>{{a.teacher}}
                <br/>
                <b>时间：</b>{{a.time}}
              </span>
            </div>
          </router-link>
          <el-divider></el-divider>
        </div>
      </div>
      </el-row>
    </el-card>
  </div>
</template>

<script>
import { article } from '../../../static/js/article.js'
import { tool } from '../../../static/js/tool.js'
import { project } from '../../../static/js/project.js'
import { vedio } from '../../../static/js/vedio.js'
import { doc } from '../../../static/js/doc.js'

export default {
  data () {
    return {
      type: this.$route.query.type,
      content: [],
      textSize: 1000
    }
  },
  created () {
    this.initData()
    window.addEventListener('resize', this.handleResize)
    this.handleResize()
  },
  methods: {
    initData: function () {
      switch (this.type) {
        case 'article':
          this.content = article
          break
        case 'tool':
          this.content = tool
          break
        case 'project':
          this.content = project
          break
        case 'vedio':
          this.content = vedio
          break
        case 'doc':
          this.content = doc
          break
      }
    },
    handleResize (event) {
      this.fullWidth = document.documentElement.clientWidth
      // 字数限制
      if (this.fullWidth > 500) {
        this.textSize = 800
      } else {
        this.textSize = 35
      }
    }
  },
  beforeDestroy: function () {
    window.removeEventListener('resize', this.handleResize)
  }
}
</script>

<style scoped>
.list-introduce {
  padding: 0.8rem;
  line-height: 23px;
  height: 10rem;
}
.img {
  margin: 0.8rem;
  float: left;
  width: 5rem;
  height: 8rem;
}
.remark {
  margin-top: 10px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
</style>



